import { __ } from "@wordpress/i18n";
import { prefix } from "../../constants";
import { useState, useCallback } from '@wordpress/element';
import Popup from "reactjs-popup";
import FileImporter from "../importer/file-importer";
import Demo from "../importer/demo-content";
import { hideLoader } from "../importer/helper";
import AddPageDemoTab from "../importer/add-page-demo-tab";
import UserFavourite from "../importer/user-favourite";
import { useEffect } from "@wordpress/element";

const Importer = () => {
    const [open, setOpen] = useState(false);
    const [showLoader, setShowLoader] = useState(true);
    const [currentParentCategory, setCurrentParentCategory] = useState(null);
    const [data, setData] = useState([]);
    const [parent, setParent] = useState([]);
    const [demo, setDemo] = useState([]);
    const [isFileMode, setIsFileMode] = useState(false);
    const [isShowingLatest, setIsShowingLatest] = useState(true);
    const [isInserting, setIsInserting] = useState(false);
    const [newTag, setNewTag] = useState(false);
    const [cache, setCache] = useState([]);
    const [activeTab, setActiveTab] = useState('Front Pages');
    const [spanClass, setSpanClass] = useState("dashicon dashicons dashicons-image-rotate");
    const [refreshCallback, setRefreshCallback] = useState(null);
    const [isRefreshed, setIsRefreshed] = useState(false);

    useEffect(() => {
        if (demo.length === 0) {
            setShowLoader(true);
            jQuery.ajax({
                url: WC_Booster_VAR.ajax_url,
                method: "GET",
                data: {
                    action: "wc_booster_fetch_front_page_demo", 
                },
                success: (response) => {
                    setDemo(response.data);
                    setShowLoader(false);
                },
                error: (xhr, status, error) => {
                    console.error("AJAX error:", error);
                    setShowLoader(false);
                },
            });
        }
    }, []);


    // Set the refresh callback once
    const refreshCallbackfunc = useCallback((callback) => {
        setRefreshCallback(() => callback);
    }, []);


    const handleRefresh = () => {
        setIsRefreshed(true);
        setShowLoader(true);
            jQuery.ajax({
                url: WC_Booster_VAR.ajax_url,
                method: "GET",
                data: {
                    action: "wc_booster_refresh_demo_data", 
                },
                success: (response) => {
                    setShowLoader(false);
                },
                error: (xhr, status, error) => {
                    console.error("AJAX error:", error);
                    setShowLoader(false);
                },
            });
    };

    const onOpen = () => {
        setOpen(true);
        jQuery("body").addClass(`${prefix}-importer-popup-open`);
        jQuery("body").addClass("loader");
    };

    const onClose = () => {
        setOpen(false);
        setIsInserting(false);
        jQuery("body").removeClass(`${prefix}-importer-popup-open`);
    };


    const handleLoaderClick = () => {
        setSpanClass("loader-1");

        setTimeout(() => {
            setSpanClass("dashicon dashicons dashicons-image-rotate");
        }, 2000);

         if (refreshCallback) {
            refreshCallback();
        }
    };

    return (
        <div>
            <button className={`wc-booster-new-importer`} onClick={onOpen}
            >
                {__("Demo Importer", "wc-booster")}

            </button>

            <Popup open={open} closeOnDocumentClick onClose={onClose}>
                <div>
                    <div className="new-demo-importer">
                        <div className="demo-importer-button">
                            <span
                                className={spanClass}
                                onClick={handleRefresh}
                            ></span>
                            <a className="close" onClick={onClose}>
                                &times;
                            </a>
                        </div>

                        <div className="importer-top-category">
                            <div className="top-category-list">
                                <ul>
                                    <AddPageDemoTab 
                                        action="fetch_pattern_demo"
                                        title="Patterns"
                                        setDemo={setDemo}
                                        setActiveTab={setActiveTab}
                                        activeTab={activeTab}
                                        setShowLoader={setShowLoader}
                                        displayCount={false}
                                        setIsFileMode={setIsFileMode}
                                        isRefreshed={isRefreshed}
                                        setIsRefreshed={setIsRefreshed}
                                    />

                                    <li className={activeTab == "file-manager" ? "active" : ""}>
                                        <a 
                                        href=""
                                        onClick={(e) => {
                                            e.preventDefault();
                                            hideLoader(setShowLoader);
                                            setIsFileMode(true);
                                            setActiveTab("file-manager");
                                        }}
                                        >
                                            <span className="dashicons dashicons-media-interactive"></span>
                                            File
                                        </a>
                                    </li>
                                    <UserFavourite
                                        setDemo={setDemo}
                                        activeTab={activeTab}
                                        setActiveTab={setActiveTab}
                                        setIsFileMode={setIsFileMode}
                                        setShowLoader={setShowLoader}
                                        onRefresh={refreshCallbackfunc}
                                        action="fetch_user_favourite_demo"

                                    />
                                </ul>
                            </div>
                        </div>
                        {
                            isFileMode && (
                                <FileImporter setIsInserting={setIsInserting} onClose={onClose} onRefresh={refreshCallbackfunc} />
                            )
                        }
                        <div className="importer-left-category">
                            <div className="left-category-list">
                                <ul>
                                    <AddPageDemoTab 
                                        action="fetch_front_page_demo"
                                        title="Front Pages"
                                        setDemo={setDemo}
                                        setActiveTab={setActiveTab}
                                        activeTab={activeTab}
                                        setShowLoader={setShowLoader}
                                        displayCount={true}
                                        setIsFileMode={setIsFileMode}
                                        isRefreshed={isRefreshed}
                                        setIsRefreshed={setIsRefreshed}
                                    />
                                    <AddPageDemoTab 
                                        action="fetch_single_product_demo"
                                        title="Single Product Page"
                                        setDemo={setDemo}
                                        setActiveTab={setActiveTab}
                                        activeTab={activeTab}
                                        setShowLoader={setShowLoader}
                                        displayCount={true}
                                        setIsFileMode={setIsFileMode}
                                        isRefreshed={isRefreshed}
                                        setIsRefreshed={setIsRefreshed}
                                    />
                                </ul>
                            </div>
                        </div>

                        {!isFileMode && !showLoader && (
                            <Demo
                            demo={demo}
                            data={data}
                            currentParentCategory={currentParentCategory}
                            newTag={newTag}
                            setNewTag={setNewTag}
                            setIsInserting={setIsInserting}
                            isInserting={isInserting}
                            onClose={onClose}
                            />
                        )}
                        {showLoader && <div className="loader-2"></div>}
                    </div>
                </div>
            </Popup>
        </div>
    );
};

export default Importer;
