import { __ } from "@wordpress/i18n";

import { MediaUpload, MediaUploadCheck } from "@wordpress/block-editor";

import { isUndefined, globalize } from "../helpers";
import { prefix } from "../constants";

const VideoControl = (props) => {
	const getValue = () => {
		const { value } = props;

		if (isUndefined(value)) {
			return {
				url: undefined,
			};
		}

		return value;
	};

	const onRender = ({ open }) => {
		const onRemove = (e) => {
			e.preventDefault();
			props.onSelect(undefined);
		};

		const { url } = getValue();

		return (
			<div className={`${prefix}-image-uploader`}>
				<div className={`${prefix}-image-uploader-wrapper`}>
					<div className={`${prefix}-button-wrapper`}>
						<button
							onClick={open}
							className={`${prefix}-image-uploader-trigger video`}
						></button>
						{url && (
							<button
								className={`${prefix}-image-uploader-delete`}
								isDestructive
								onClick={onRemove}
							>
								{__("Delete Video", "wc-booster")}
							</button>
						)}
					</div>
				</div>
			</div>
		);
	};

	const onSelect = (v, type) => {
		let d;

		const { url, height, width, subtype, mime, icon } = v;

		d = {
			id: v.id,
			alt: v.alt,
			url,
			height,
			width,
			subtype,
			mime,
			icon,
		};

		props.onSelect(d);
	};

	const { value } = props;

	let selectedId = undefined;
	if (value) {
		selectedId = value.id;
	}

	return (
		<>
			<MediaUploadCheck>
				<MediaUpload
					gallery={false}
					multiple={false}
					onSelect={(v) => {
						onSelect(v);
					}}
					allowedTypes={["video"]}
					value={selectedId}
					render={onRender}
				/>
			</MediaUploadCheck>
		</>
	);
};

export default VideoControl;

globalize("VideoControl", VideoControl);
