import { useEffect, useState, useCallback } from '@wordpress/element';
import { useSelect } from '@wordpress/data';
import { __ } from "@wordpress/i18n";

const dataCache = {};

const UserFavourite = ({ setDemo, activeTab, setActiveTab, setIsFileMode, setShowLoader, onRefresh, action }) => {

    const [cache, setCache] = useState({});
    const favData = useSelect((select) => {
        const { getCurrentUser } = select("core");
        const currentUser = getCurrentUser();
        return currentUser;
    });

    useEffect(() => {
        if (!favData) return;

        jQuery.ajax({
            url: WC_Booster_VAR.ajax_url,
            method: "GET",
            data: { action: `wc_booster_${action}` },
            success: (response) => {
                const favourite = response.data.demos.filter((demo) =>
                    favData?.wc_booster_favourites_demo
                        ? Object.values(favData.wc_booster_favourites_demo).includes(demo.id.toString())
                        : false
                );
                setCache(prevCache => ({
                    ...prevCache,
                    userfavourite: { data: favourite }
                }));
            },
            error: (xhr, status, error) => {
                console.error("AJAX error:", error);
                setShowLoader(false);
            },
        });
    }, [favData]);

    const handleClick = (e) => {
        e.preventDefault();
        setIsFileMode(false);
        const { data } = cache["userfavourite"] || {};
        if (data) {
            setDemo(data);
        }
    };

    return (
        <li className={activeTab === "user-favorite" ? "active" : ""}>
            <a
                href="#"
                onClick={(e) => {
                    e.preventDefault();
                    handleClick(e);
                    setActiveTab("user-favorite");
                }}
            >
                <span className="dashicons dashicons-heart"></span>
                {__("Favourite", "wc-booster")}
            </a>
        </li>
    );
};

export default UserFavourite;
