import { __ } from "@wordpress/i18n";
import {
	InspectorControls,
	InspectorAdvancedControls,
} from "@wordpress/block-editor";
import {
	ColorIndicator,
	PanelBody,
	SelectControl,
    RangeControl
} from "@wordpress/components";

import { createHooks } from "@wordpress/hooks";

import {
	ColorControl,
	BgControl,
	BackgroundPosition,
	FixedBackgroundColor,
	BackgroundOverlayControl,
	SwitchControl,
} from "../../../custom-control";

import ResponsiveRangeWrapperControl from "../../../custom-control/responsive-range-wrapper";
import ImageControl from "../../../custom-control/image-control";
import VideoControl from "../../../custom-control/video-control";
import DimensionControl from "../../../custom-control/dimension";
import Exporter from "../../../components/exporter";
import { getShapeDivider } from "../../../helpers";
import Tab from "../../../components/tab";
import { prefix } from "../../../constants";
import metadata from "../block.json";

const InspectorPanel = (props) => {
	const {
		attributes: {
			padding,
			margin,
			bgType,
			bgImage,
			bgVideo,
			bgPosition,
			bgColor,
			bgOverlay,
			containerType,
			containerWidth,
			bgAttachment,
			enableShapeDivider,
			shapeHeight = metadata.attributes.shapeHeight.default,
			shapeColor = metadata.attributes.shapeColor.default,
			shape = metadata.attributes.shape.default,
			shapeBgColor = metadata.attributes.shapeBgColor.default,
			shapePosition = metadata.attributes.shapePosition.default,
			shapeZIndex = metadata.attributes.shapeZIndex.default,
		},
		setAttributes,
		clientId,
	} = props;

	const { applyFilters } = createHooks();

	const shapes = applyFilters("riseBlocksShapes", [
		{
			shape: "hills",
			label: __("Hills", "wc-booster"),
		},
		{
			shape: "angled",
			label: __("Angled", "wc-booster"),
		},
		{
			shape: "waves",
			label: __("Waves", "wc-booster"),
		},
	]);

	const bgData = {
		shapeColor: {
			value: shapeColor,
			title: __("Color", "wc-booster"),
		},
		shapeBgColor: {
			value: shapeBgColor,
			title: __("Background", "wc-booster"),
		},
	};

	const shapeColorSetting = Object.keys(bgData).map((key) => ({
		title: () => (
			<div>
				{bgData[key].title}
				<ColorIndicator colorValue={bgData[key].value} />
			</div>
		),
		content: () => (
			<ColorControl
				value={bgData[key].value}
				hideIndicator={true}
				onChange={(c) => setAttributes({ [key]: c })}
			/>
		),
	}));

	return (
		<>
			<InspectorAdvancedControls>
				<Exporter clientId={clientId} />
			</InspectorAdvancedControls>
			<InspectorControls>
				<PanelBody title={__("General", "wc-booster")} initialOpen={true}>
					<SelectControl
						label={__("Container Type", "wc-booster")}
						options={[
							{ label: __("Boxed", "wc-booster"), value: "boxed" },
							{ label: __("Full Width", "wc-booster"), value: "full-width" },
						]}
						value={containerType}
						onChange={(containerType) => setAttributes({ containerType })}
					/>
					{"boxed" == containerType && (
						<ResponsiveRangeWrapperControl
							defaultValue={metadata.attributes.containerWidth.default}
							value={containerWidth}
							onChange={(containerWidth) => {  setAttributes({ containerWidth }) } }
							label={__("Width", "wc-booster")}
							beforeIcon=""
						/>
					)}
				</PanelBody>

				<PanelBody title={__("Shape", "wc-booster")} initialOpen={false}>
					<SwitchControl
						label={__("Enable Shape Divider", "wc-booster")}
						checked={enableShapeDivider}
						onChange={(enableShapeDivider) => {
							setAttributes({ enableShapeDivider });
						}}
					/>
					{enableShapeDivider && (
						<>
							<SelectControl
								label={__("Position", "wc-booster")}
								options={[
									{ label: __("Top", "wc-booster"), value: "top" },
									{ label: __("Bottom", "wc-booster"), value: "bottom" },
								]}
								value={shapePosition}
								onChange={(shapePosition) => setAttributes({ shapePosition })}
							/>
							<div className={`shape-divider-control`}>
								{shapes.map((v, i) => (
									<button
										key={i}
										className={`${v.shape == shape ? "selected" : ""}`}
										onClick={(e) => setAttributes({ shape: v.shape })}
									>
										{getShapeDivider(v.shape, {
											height: "25px",
											width: "100%",
											fill: "#000000",
										})}
										<label>{v.label}</label>
									</button>
								))}
							</div>
							<ResponsiveRangeWrapperControl
								defaultValue={metadata.attributes.shapeHeight.default}
								value={shapeHeight}
								onChange={(shapeHeight) => setAttributes({ shapeHeight })}
								label={__("Height", "wc-booster")}
								beforeIcon=""
							/>

							<Tab
								className={`${prefix}-tabbed-color-control`}
								label={__("Shape Color", "wc-booster")}
								data={shapeColorSetting}
							/>

							<RangeControl
								label={__("Z-Index", "wc-booster")}
								allowReset={true}
								min={0}
								max={10000}
								value={shapeZIndex}
								onChange={(shapeZIndex) => setAttributes({ shapeZIndex })}
							/>
						</>
					)}
				</PanelBody>

				<PanelBody title={__("Background", "wc-booster")} initialOpen={false}>
					<BgControl
						label={__("Type", "wc-booster")}
						onChange={(bgType) => setAttributes({ bgType })}
						value={bgType}
					/>

					{bgType == "image" && (
						<>
							<ImageControl
								onSelect={(bgImage) => {
									setAttributes({ bgImage });
								}}
								value={bgImage}
							/>

							{bgImage && bgImage.url && (
								<>
									<BackgroundPosition
										value={bgPosition}
										onChange={(bgPosition) => setAttributes({ bgPosition })}
									/>
									<FixedBackgroundColor
										checked={bgAttachment}
										onChange={(bgAttachment) => setAttributes({ bgAttachment })}
									/>

									<BackgroundOverlayControl
										value={bgOverlay}
										onChange={(bgOverlay) => setAttributes({ bgOverlay })}
									/>
								</>
							)}
						</>
					)}

					{bgType == "video" && (
						<>
							<VideoControl
								onSelect={(bgVideo) => {
									setAttributes({ bgVideo });
								}}
								value={bgVideo}
							/>
							<BackgroundOverlayControl
								value={bgOverlay}
								onChange={(bgOverlay) => setAttributes({ bgOverlay })}
							/>
						</>
					)}

					{bgType == "color" && (
						<ColorControl
							value={bgColor}
							hideIndicator={true}
							onChange={(bgColor) => setAttributes({ bgColor })}
						/>
					)}
				</PanelBody>

				<PanelBody title={__("Spacing", "wc-booster")} initialOpen={false}>
					<DimensionControl
						label={__("Section Padding", "wc-booster")}
						defaultValue={metadata.attributes.padding.default}
						value={padding}
						onChange={(padding) => setAttributes({ padding })}
					/>
					<DimensionControl
						label={__("Section Margin", "wc-booster")}
						defaultValue={metadata.attributes.margin.default}
						value={margin}
						onChange={(margin) => setAttributes({ margin })}
					/>
				</PanelBody>
			</InspectorControls>
		</>
	);
};

export default InspectorPanel;
