import { useState, useEffect } from '@wordpress/element';

// Global cache object
const dataCache = {};

const AddPageDemoTab = ({
    action,
    title,
    setDemo,
    setActiveTab,
    activeTab,
    setShowLoader,
    displayCount,
    setIsFileMode,
    setIsRefreshed,
    isRefreshed
}) => {
    const [count, setCount] = useState(dataCache[action]?.count || null);

    useEffect(() => {
        if (isRefreshed) {
            delete dataCache[action];
            setIsRefreshed(false);
        }
    }, [isRefreshed]);

    useEffect(() => {
        if (!dataCache[action]) {
            setShowLoader(true);
            jQuery.ajax({
                url: WC_Booster_VAR.ajax_url,
                method: "GET",
                data: { action: `wc_booster_${action}` },
                success: (response) => {
                    const responseCount = response.data.length;
                    dataCache[action] = { data: response.data, count: responseCount };
                    setCount(responseCount);
                    setShowLoader(false);
                },
                error: (xhr, status, error) => {
                    console.error("AJAX error:", error);
                    setShowLoader(false);
                },
            });
        } else {
            setCount(dataCache[action].count);
        }
    }, [action, isRefreshed, setShowLoader, setIsRefreshed]);

    const handleClick = (e) => {
        e.preventDefault();
        setIsFileMode(false);
        const { data, count } = dataCache[action];
        setCount(count);
        setDemo(data);
        setActiveTab(title);
    };

    return (
        <li className={activeTab === title ? 'active' : ''}>
            <a href="#" onClick={handleClick}>
                {title}
                {displayCount !== false && <span>{count}</span>}
            </a>
        </li>
    );
};

export default AddPageDemoTab;
