import React from "react";
import { __ } from "@wordpress/i18n";
import { prefix } from "../../constants";
import {  isPro } from "../../helpers";
import { useSelect } from '@wordpress/data';
import { insertBlock } from "../importer/helper";
import { useEffect } from "@wordpress/element";
import { isSingleProductTemplate } from "../../helpers";

const Demo = ({
    demo,
    data,
    currentParentCategory,
    newTag,
    setNewTag,
    setIsInserting,
    isInserting,
    onClose,
}) => {

    const isNew = (date) => {
        const today = new Date();
        const dateObj = new Date(date);
        const totalMonth =
        today.getMonth() -
        dateObj.getMonth() +
        12 * (today.getFullYear() - dateObj.getFullYear());
        return totalMonth <= 1;
    };

    useEffect(() => {
        const hasNewTag = demo.some((node) => isNew(node.date));
        if (hasNewTag && !newTag) {
            setNewTag(true);
        }
    }, [demo, newTag, setNewTag]);

    const favData = useSelect((select) => {
        const { getCurrentUser } = select("core");
        const currentUser = getCurrentUser();
        return currentUser;
    });

    const addToFavourite = (id) => {
        jQuery.post(WBCF.ajax_url, {
            action: "wc_booster_update_user_favourites",
            id,
            security: WBCF.wc_ajax_nonce,
        });
    };

    return (
       <div className="importer-data-box">
            {demo.length > 0 &&
                demo.map((node) => {
                    let img = "https://placehold.co/400x500";
                    if (node._embedded && node._embedded["wp:featuredmedia"]) {
                        try {
                            let temp =
                            node._embedded["wp:featuredmedia"][0].media_details.sizes;
                            temp = temp["full"];
                            img = temp.source_url;
                        } catch (e) {}
                    }

                    let preview =
                        node.preview_link === "" ? node.link : node.preview_link;

                    return (
                        <div
                            key={node.id}
                            className="data-box-item"
                        >
                            {isNew(node.date) && (
                                <div className={`${prefix}-test-new-block`}>
                                    <span>{__("New", "wc-booster")}</span>
                                </div>
                            )}
                            <div className="data-box-inner">
                                {img && (
                                    <img
                                        src={img}
                                        alt=""
                                        loading="lazy"
                                    />
                                )}
                                <a href={preview} target="_blank"><span className="dashicons dashicons-visibility"></span></a>
                            </div>
                            <div className="data-box-content">
                                {node.is_pro === "" || (node.is_pro === "1" && isPro()) ? (
                                    <a
                                        href="#"
                                        className={`${prefix}-insert-button`}
                                        onClick={(e) => {

                                            if ( isSingleProductTemplate && node.is_single_product_demos ) {

                                                const userConfirmed = window.confirm(__('This demos are for single product page template only are you sure you want to insert ?', 'wc-booster'));
                                                if (userConfirmed  && !isInserting) {
                                                    insertBlock(node.demo_json, e, setIsInserting, onClose);
                                                }
                                            } else {
                                                if (!isInserting) {
                                                    insertBlock(node.demo_json, e, setIsInserting, onClose);
                                                }
                                            }

                                        }}
                                    >
                                    <span className="dashicons dashicons-download"></span>
                                    {__("Insert ", "wc-booster")}
                                    </a>
                                    ) : (
                                    <a href="" target="_blank" className="importer-get-pro">Get Pro <span className="dashicons dashicons-arrow-right-alt"></span></a>
                                )}
                                
                                <a
                                href="#"
                                className={`importer-favourite-button ${
                                    favData &&
                                    Object.values(favData.wc_booster_favourites_demo).includes(
                                        node.id.toString(),
                                        )
                                    ? "favourite"
                                    : ""
                                }`}
                                onClick={(e) => {
                                    const $this = jQuery(e.currentTarget);
                                    if ($this.hasClass("favourite")) {
                                        $this.removeClass("favourite");
                                    } else {
                                        $this.addClass("favourite");
                                    }
                                    addToFavourite(node.id);
                                }}
                                >
                                    <span className="dashicons dashicons-heart">
                                    </span>
                                </a>
                            </div>
                        </div>
                        );

                })
            }
        </div>
    );
};

export default Demo;
