import { prefix } from "../../constants";
import { __ } from "@wordpress/i18n";
import { insertBlock } from "../importer/helper";
import { useEffect } from '@wordpress/element';

const importerName = __("Import", "wc-booster");
const importingName = __("Importing...", "wc-booster");

const FileImporter = ( { setIsInserting, onClose, onRefresh  } ) => {

	const importFromFile = (e) => {
		const $input = jQuery(`input[name="${prefix}-json-file"]`);
		const $this = jQuery(e.target);
		const file = $input.prop("files")[0];
		$this.blur();
		if (file) {
			$this.addClass("is-busy").text(importingName);
			const reader = new FileReader();
			reader.onload = () => {
				const data = reader.result;
				try {
					const json = JSON.parse(data);
					insertBlock(json, e, setIsInserting);
					$input.val("");
				} catch (e) {
					console.warn(e.message);
				}

				insertBlock(reader.result, e, setIsInserting, onClose);
				$this.removeClass("is-busy").text(importerName);
			};
			reader.readAsText(file);
		}
	};

	return (
        <div className={`${prefix}-importer-data-box-wrapper`}>
            <div className={`${prefix}-import-popup-file`}>
                <input type="file" name={`${prefix}-json-file`} />
                <button
                    onClick={importFromFile}
                    className={`button components-button button-primary button-large ${prefix}-importer-action`}
                >
                    {importerName}
                </button>
            </div>
        </div>
    );
}

export default FileImporter;
