import { InnerBlocks } from "@wordpress/block-editor";
import { prefix } from "../../../constants";
import { getShapeDivider, isUndefined } from "../../../helpers";

const Save = (props) => {
	const {
		attributes: {
			block_id,
			bgType,
			enableShapeDivider,
			containerType,
			bgVideo,
			verticalFlip,
			horizontalFlip,
			shape,
			shapePosition,
		},
	} = props;

	const renderShape = () => {
		let shapeClass = "";
		if (horizontalFlip) {
			shapeClass += "horizontally-flipped";
		}

		if (verticalFlip) {
			shapeClass += shapeClass.length === 0 ? "" : " ";
			shapeClass += "vertically-flipped";
		}

		return (
			<div className={`${prefix}-section-shape ${shapePosition} ${shapeClass}`}>
				{getShapeDivider(shape, {})}
			</div>
		);
	};

	return (
		<div id={block_id} className="wp-block-wc-booster-section">
			<div className={`${prefix}-${containerType} ${prefix}-section-wrapper`}>
				{enableShapeDivider && renderShape()}

				{"video" === bgType && !isUndefined(bgVideo) && (
					<>
						<div className={`${prefix}-section-overlay`}></div>
						<video id={`${block_id}-video`} autoPlay muted loop>
							<source src={bgVideo.url} type={bgVideo.mime} />
							Your browser does not support HTML5 video.
						</video>
					</>
				)}

				{"image" === bgType && (
					<div className={`${prefix}-section-overlay`}></div>
				)}

				<div className={`${prefix}-section-inner`}>
					<InnerBlocks.Content />
				</div>
			</div>
		</div>
	);
};

export default Save;
