import "../styles/editor.scss";
import { withInstanceId } from "@wordpress/compose";
import {
	BlockControls,
	InnerBlocks,
	useBlockProps,
} from "@wordpress/block-editor";
import { __ } from "@wordpress/i18n";
import { Toolbar, SVG, Path, G } from "@wordpress/components";
import { prefix } from "../../../constants";
import {
	getDimensionStyle,
	getShapeDivider,
	getResponsiveRangeVal,
	isUndefined,
} from "../../../helpers";
import { useEffect } from "@wordpress/element";

import Inspector from "./inspector";
import metadata from "../block.json";

const Edit = (props) => {
	const {
		attributes: {
			block_id,
			padding,
			margin,
			bgImage,
			bgVideo,
			bgPosition,
			bgColor,
			bgType,
			bgAttachment,
			bgOverlay,
			enableShapeDivider,
			verticalFlip = metadata.attributes.verticalFlip.default,
			horizontalFlip = metadata.attributes.horizontalFlip.default,
			shapeColor = metadata.attributes.shapeColor.default,
			shapeHeight = metadata.attributes.shapeHeight.default,
			shape = metadata.attributes.shape.default,
			shapePosition = metadata.attributes.shapePosition.default,
			shapeBgColor = metadata.attributes.shapeBgColor.default,
			shapeZIndex = metadata.attributes.shapeZIndex.default,
		},
		setAttributes,
		instanceId,
		clientId,
	} = props;

	useEffect(() => {
		const id = `${prefix}-section-block-instance-${instanceId}-${clientId}`;
		setAttributes({
			block_id: id,
		});
	}, []);

	useEffect(() => {
		play();
	});

	const play = () => {
		if ("video" == bgType && !isUndefined(bgVideo)) {
			let video = document.getElementById(block_id + "-video");
			if (video) {
				let source = document.getElementById(block_id + "-video-source");
				if (source.getAttribute("src") != bgVideo.url) {
					video.load();
				} else {
					video.play();
				}
			}
		}
	};

	const renderShape = () => {
		let shapeStyle = {
			backgroundColor: shapeBgColor,
			zIndex: shapeZIndex,
		};

		if (shapePosition == "top" && !verticalFlip) {
			shapeStyle.marginBottom = `-${getResponsiveRangeVal(shapeHeight)}`;
		} else if (verticalFlip) {
			shapeStyle.marginTop = `-${getResponsiveRangeVal(shapeHeight)}`;
		}

		let shapeClass = "";
		if (horizontalFlip) {
			shapeClass += "horizontally-flipped";
		}

		if (verticalFlip) {
			shapeClass += shapeClass.length == 0 ? "" : " ";
			shapeClass += "vertically-flipped";
		}

		return (
			<div
				style={shapeStyle}
				className={`${prefix}-section-shape ${shapePosition} ${shapeClass}`}
			>
				{getShapeDivider(shape, {
					height: getResponsiveRangeVal(shapeHeight),
					fill: shapeColor,
				})}
			</div>
		);
	};

	const style = {
		backgroundPosition: bgPosition,
		...getDimensionStyle(
			["paddingTop", "paddingRight", "paddingBottom", "paddingLeft"],
			padding,
		),
		...getDimensionStyle(
			["marginTop", "marginRight", "marginBottom", "marginLeft"],
			margin,
		),
		backgroundAttachment: bgAttachment ? "fixed" : "",
	};

	if ("image" == bgType && bgImage && bgImage.url) {
		style.backgroundImage = `url(${bgImage.url})`;
	} else if ("color" == bgType && bgColor) {
		style.backgroundColor = bgColor;
	}

	const overlayStyle = {
		backgroundColor: bgOverlay,
	};

	const toolbarControls = [
		{
			icon: (
				<SVG fill="none" viewBox="0 0 24 24" xmlns="http://www.w3.org/2000/svg">
					<G fill="currentColor">
						<Path d="m6 5.5h12c.2761 0 .5.22386.5.5v3h1.5v-3c0-1.10457-.8954-2-2-2h-12c-1.10457 0-2 .89543-2 2v3h1.5v-3c0-.27614.22386-.5.5-.5z" />
						<Path d="m22 13h-20v-2h20z" />
						<Path d="m20 16.5h-1.5v-1.5h1.5z" />
						<Path d="m18 20c1.1046 0 2-.8954 2-2h-1.5c0 .2761-.2239.5-.5.5z" />
						<Path d="m7.5 18.5h2v1.5h-2z" />
						<Path d="m11 20v-1.5h2v1.5z" />
						<Path d="m5.5 18c0 .2761.22386.5.5.5v1.5c-1.10457 0-2-.8954-2-2z" />
						<Path d="m4 16.5h1.5v-1.5h-1.5z" />
						<Path d="m14.5 18.5v1.5h2v-1.5z" />
					</G>
				</SVG>
			),
			title: __("Flip vertically", "wc-booster"),
			isActive: !!verticalFlip,
			onClick: () => setAttributes({ verticalFlip: !verticalFlip }),
		},
		{
			icon: (
				<SVG fill="none" viewBox="0 0 24 24" xmlns="http://www.w3.org/2000/svg">
					<G fill="currentColor">
						<Path d="m5.5 18v-12c0-.27614.22386-.5.5-.5h3v-1.5h-3c-1.10457 0-2 .89543-2 2v12c0 1.1046.89543 2 2 2h3v-1.5h-3c-.27614 0-.5-.2239-.5-.5z" />
						<Path d="m13 2v20h-2v-20z" />
						<Path d="m16.5 4v1.5h-1.5v-1.5z" />
						<Path d="m20 6c0-1.10457-.8954-2-2-2v1.5c.2761 0 .5.22386.5.5z" />
						<Path d="m18.5 16.5v-2h1.5v2z" />
						<Path d="m20 13h-1.5v-2h1.5z" />
						<Path d="m18 18.5c.2761 0 .5-.2239.5-.5h1.5c0 1.1046-.8954 2-2 2z" />
						<Path d="m16.5 20v-1.5h-1.5v1.5z" />
						<Path d="m18.5 9.5h1.5v-2h-1.5z" />
					</G>
				</SVG>
			),
			title: __("Flip horizontally", "wc-booster"),
			isActive: !!horizontalFlip,
			onClick: () => setAttributes({ horizontalFlip: !horizontalFlip }),
		},
	];

	const blockProps = useBlockProps({
        style: style
    });

	return (
		<>
			{enableShapeDivider && (
				<BlockControls>
					<Toolbar controls={toolbarControls} />
				</BlockControls>
			)}

			<Inspector {...props} />

			<div {...blockProps}>
				{enableShapeDivider && renderShape()}

				{"video" == bgType && !isUndefined(bgVideo) && (
					<>
						<div
							className={`${prefix}-section-overlay`}
							style={overlayStyle}
						></div>
						<video id={`${block_id}-video`} autoPlay muted loop>
							<source
								id={`${block_id}-video-source`}
								src={bgVideo.url}
								type={bgVideo.mime}
							/>
							Your browser does not support HTML5 video.
						</video>
					</>
				)}

				{play()}
				{"image" == bgType && (
					<div
						className={`${prefix}-section-overlay`}
						style={overlayStyle}
					></div>
				)}

				<InnerBlocks templateLock={false} />
			</div>
		</>
	);
};

export default withInstanceId(Edit);
