import { createBlock } from "@wordpress/blocks";
import { dispatch } from '@wordpress/data';

export const endPoint = "https://demos.wcbooster.com/wp-json/wp/v2/";

export const createBlockRecursively = ({ name, attributes, inner = false }) => {
    if (inner) {
        return createBlock(
            name,
            attributes,
            inner.map((node) => createBlockRecursively(node)),
        );
    }
    return createBlock(name, attributes);
};

export const insertBlock = (json, e, setIsInserting, onClose) => {

    setIsInserting(true);
    try {
        e.preventDefault();
        for (var i = 0; i < json.length; i++) {
            const data = json[i];
            if (data.name) {
                var created = createBlockRecursively(data);
                dispatch("core/block-editor").insertBlock(created);
            }
        }

        onClose();
    } catch (e) {
        setIsInserting(false);
        console.warn(e.message);
    }
};

export const hideLoader = ( setShowLoader ) => {
  setShowLoader(false);
};